        "language": "bg",
        "about_en": "With international populations on the rise, PricewaterhouseCoopers expects global trade in goods and services to increase to $27 trillion by 2030. As global trade grows, so will the need for effective logistics management \u2014 ensuring the supply of goods and services closely matches the demand. The W. P. Carey business degree in global logistics management prepares you to meet this need and thrive in dynamic organizations worldwide.\r\n\r\nWith international populations on the rise, PricewaterhouseCoopers expects global trade in goods and services to increase to $27 trillion by 2030. As global trade grows, so will the need for effective logistics management \u2014 ensuring the supply of goods and services closely matches the demand. The W. P. Carey business degree in global logistics management prepares you to meet this need and thrive in dynamic organizations worldwide.\r\n\r\nWith international populations on the rise, PricewaterhouseCoopers expects global trade in goods and services to increase to $27 trillion by 2030. As global trade grows, so will the need for effective logistics management \u2014 ensuring the supply of goods and services closely matches the demand. The W. P. Carey business degree in global logistics management prepares you to meet this need and thrive in dynamic organizations worldwide.",
        "about_bg": "\u0421 \u0443\u0432\u0435\u043b\u0438\u0447\u0430\u0432\u0430\u043d\u0435\u0442\u043e \u043d\u0430 \u043c\u0435\u0436\u0434\u0443\u043d\u0430\u0440\u043e\u0434\u043d\u043e\u0442\u043e \u043d\u0430\u0441\u0435\u043b\u0435\u043d\u0438\u0435 PricewaterhouseCoopers \u043e\u0447\u0430\u043a\u0432\u0430 \u0433\u043b\u043e\u0431\u0430\u043b\u043d\u0430\u0442\u0430 \u0442\u044a\u0440\u0433\u043e\u0432\u0438\u044f \u0441\u044a\u0441 \u0441\u0442\u043e\u043a\u0438 \u0438 \u0443\u0441\u043b\u0443\u0433\u0438 \u0434\u0430 \u043d\u0430\u0440\u0430\u0441\u043d\u0435 \u0434\u043e 27 \u0442\u0440\u0438\u043b\u0438\u043e\u043d\u0430 \u0434\u043e\u043b\u0430\u0440\u0430 \u0434\u043e 2030 \u0433. \u0421 \u043d\u0430\u0440\u0430\u0441\u0442\u0432\u0430\u043d\u0435\u0442\u043e \u043d\u0430 \u0441\u0432\u0435\u0442\u043e\u0432\u043d\u0430\u0442\u0430 \u0442\u044a\u0440\u0433\u043e\u0432\u0438\u044f \u0449\u0435 \u043d\u0430\u0440\u0430\u0441\u0442\u0432\u0430 \u0438 \u043d\u0435\u043e\u0431\u0445\u043e\u0434\u0438\u043c\u043e\u0441\u0442\u0442\u0430 \u043e\u0442 \u0435\u0444\u0435\u043a\u0442\u0438\u0432\u043d\u043e \u0443\u043f\u0440\u0430\u0432\u043b\u0435\u043d\u0438\u0435 \u043d\u0430 \u043b\u043e\u0433\u0438\u0441\u0442\u0438\u043a\u0430\u0442\u0430 - \u0433\u0430\u0440\u0430\u043d\u0442\u0438\u0440\u0430\u043d\u0435\u0442\u043e \u043d\u0430 \u043f\u0440\u0435\u0434\u043b\u0430\u0433\u0430\u043d\u0435\u0442\u043e \u043d\u0430 \u0441\u0442\u043e\u043a\u0438 \u0438 \u0443\u0441\u043b\u0443\u0433\u0438 \u0442\u044f\u0441\u043d\u043e \u0441\u044a\u043e\u0442\u0432\u0435\u0442\u0441\u0442\u0432\u0430 \u043d\u0430 \u0442\u044a\u0440\u0441\u0435\u043d\u0435\u0442\u043e. \u0411\u0438\u0437\u043d\u0435\u0441 \u0441\u0442\u0435\u043f\u0435\u043d\u0442\u0430 \u043d\u0430 W. P. Carey \u0432 \u0433\u043b\u043e\u0431\u0430\u043b\u043d\u043e\u0442\u043e \u0443\u043f\u0440\u0430\u0432\u043b\u0435\u043d\u0438\u0435 \u043d\u0430 \u043b\u043e\u0433\u0438\u0441\u0442\u0438\u043a\u0430\u0442\u0430 \u0432\u0438 \u043f\u043e\u0434\u0433\u043e\u0442\u0432\u044f \u0434\u0430 \u043f\u043e\u0441\u0440\u0435\u0449\u043d\u0435\u0442\u0435 \u0442\u0430\u0437\u0438 \u043d\u0443\u0436\u0434\u0430 \u0438 \u0434\u0430 \u043f\u0440\u043e\u0446\u044a\u0444\u0442\u044f\u0432\u0430\u0442\u0435 \u0432 \u0434\u0438\u043d\u0430\u043c\u0438\u0447\u043d\u0438\u0442\u0435 \u043e\u0440\u0433\u0430\u043d\u0438\u0437\u0430\u0446\u0438\u0438 \u043f\u043e \u0446\u0435\u043b\u0438\u044f \u0441\u0432\u044f\u0442.",
        "objective_en": "As global markets continue to grow and change, supply chain professionals are increasingly expected to develop new strategic business processes to manage the distribution of goods and services. Building on your supply chain knowledge and experience, the Global Logistics Management professional development certificate will enhance your ability to analyze international logistics systems and strategies in relation to organizational goals and relationships. You will explore strategic issues contributing to transportation, inventory, and location management decisions, as well as assess the need for globalization of an organization\u2019s operations and how to benefit from government regulations related to international logistics and trade.\r\n\r\nMacEwan University School of Continuing Education, in association with the Supply Chain Management Association of Alberta (SCMA AB), offers this series of short online courses to meet the emerging needs of the global supply chain industry. Through case-based, problem-solving activities, you will have the opportunity to apply high level, analytical reasoning to examine different solutions to purchasing and transportation logistics, advanced sourcing, and integrated business systems in different domestic and international settings to achieve a stronger presence in the global marketplace.\r\n\r\nCourses may be taken individually. Complete all eight courses to receive the Global Logistics Management professional development certificate. If you are interested in achieving the SCMP designation",
